body {
    margin: 0;
    font-family: 'Arial', sans-serif;
    font-size: 12px;
    line-height: 1.5;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

#root {
    display: flex;
    justify-content: center;
}

.App {
    width: 793px;
}

.d-flex {
    display: flex;
}

.d-grid {
    display: grid;
}

.skill-grid {
    grid-template-columns: repeat(auto-fill, minmax(100px, 1fr));
}

.align-items-center {
    align-items: center;
}

.align-items-end {
    align-items: flex-end;
}

.flex-column {
    flex-direction: column;
}

.justify-content-between {
    justify-content: space-between;
}

.justify-content-end {
    justify-content: end;
}

.gap-0-1 {
    gap: 0 1rem;
}

.gap-0-2 {
    gap: 0 2rem;
}

.gap-0-3 {
    gap: 0 3rem;
}

.flex-wrap {
    flex-wrap: wrap;
}

.small {
    font-size: smaller;
}

.fw-500 {
    font-weight: 500;
}

.fw-600 {
    font-weight: 600;
}

.fw-bold {
    font-weight: bold;
}

.text-muted {
    color: #373737;
}

.text-nowrap {
    white-space: nowrap;
}

.text-break {
    word-wrap: break-word;
    word-break: break-word;
}

.text-justify {
    text-align: justify;
}

.text-end {
    text-align: end;
}

.text-decoration-none {
    text-decoration: none;
}

.w-70 {
    width: 70%;
}

.w-30 {
    width: 30%;
}

.mb-2 {
    margin-bottom: 0.2rem;
}

.mb-5 {
    margin-bottom: 0.5rem;
}

h1, h2, h3 {
    margin: 0;
}

h4 {
    margin-top: 0.2rem;
    margin-bottom: 0.1rem;
}

a {
    color: inherit;
}

ul {
    padding-left: 15px;
    margin: 0px;
}

svg {
    width: 12px !important;
    height: 12px !important;
}

#header {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}

.heading {
    font-size: 14px;
    font-weight: bold;
    color: #373737;
    padding-bottom: 2px;
    border-bottom: 1px solid #c7c7c7;
    margin-bottom: 5px;
}

#contact {
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-end;
    gap: 0 15px;
}

@media only screen and (max-width: 793px) {
    .App {
        width: 100%;
        padding-inline: 15px;
    }

    #contact {
        display: grid;
        grid-template-columns: auto;
        gap: 0 5px;
    }
}